import React from 'react'

export const DashboardScreen = () => {
  return (
    <div className='row dashboard'>
      <div className='col-lg-10 mx-auto'>
        <div className='cardBox'>
        <div className='cardUser'>
          <div>
            <div className='numbers'>0</div>
            <div className='cardName'>Documentos Protegidos</div>
          </div>
          <div className='iconBx'>
            <ion-icon name="document-lock-outline"></ion-icon>
          </div>
        </div>

        <div className='cardUser'>
          <div>
            <div className='numbers'>10</div>
            <div className='cardName'>Documentos Verificados</div>
          </div>
          <div className='iconBx'>
            <ion-icon name="document-attach-outline"></ion-icon>
          </div>
        </div>

        <div className='cardUser'>
          <div>
            <div className='numbers'>2</div>
            <div className='cardName'>Mensajes</div>
          </div>
          <div className='iconBx'>
            <ion-icon name="chatbubbles-outline"></ion-icon>
          </div>
        </div>

        
        <div className='cardUser'>
          <div>
            <div className='numbers'>$0</div>
            <div className='cardName'>Saldo disponible</div>
          </div>
          <div className='iconBx'>
            <ion-icon name="cash-outline"></ion-icon>
          </div>
        </div>
      </div>
    </div>
  </div>

  )
}
