const { default: Swal } = require("sweetalert2")

const url = 'https://certificador.herokuapp.com/api'
const api = {
    register: `${url}/usuarios/`, 
    update: `${url}/api/usuarios/`,
}

const registrarUsuario = (data = {}) => {

    fetch(api.register, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
            rut: data.rut,
            nombre: `${data.nombre} ${data.apellido}`,
            correo: data.correo,
            password: data.password,
            rol :"USER_ROLE",
        })
    })
    .then(res => res.json())
    .then(data => {
        if(data.errors){
            Swal.fire(
                'Error al registrar datos',
                'Por favor verifica el correcto ingreso de tus datos',
                'error'
        )}else{
            Swal.fire(
                'Registro Correcto',
                'Por favor verifica tu correo para activar tu cuenta',
                'success'
            )};
        })
        .catch((errors) => {
            console.log(`Errores: ${errors}`)
        })
}


const updatePassword = (data = '') => {
    const user = JSON.parse(localStorage.getItem('user') || {logged: false});

    fetch(`${api.register}${user.uid}`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
            password: data
        })
    })
    .then(res => res.json())
    .then(data => {
        console.log(data)
    })

}



module.exports = {
    updatePassword,
    registrarUsuario,
}