import React from 'react'

export const ProtectScreen = () => {
  return (
    <div className='row protect-zone'>
        <div className='col-lg-10'>
            <div className='dropZone'>
                <p>Arrastra aqui tu documento</p>

                <form className='form-protect'>
                  <input className='form-control w-50 mx-auto' type='file'/>
                  <center>
                   <input id='btnProtect' className='btn' type='submit' value='Proteguer' />
                  </center>
                </form>
            </div>
        </div>
    </div>
  )
}
