import React from 'react'

export const VerifyScreen = () => {
  return (
    <div className='row'>
        <div className='col-lg-10 verify'>
            <div className='dropZone-verify'>
                <h3>Arrastra aqui el documento original</h3>

                <form className='formVerify'>
                    <input className='form-control w-50 mx-auto' type='file'/>
                    <center>
                    <input id='btnVerify' className='btn' type='submit' value='Verificar' />
                    </center>
                </form>
                <p>Verificar documento con hash</p>
            </div>
        </div>
    </div>
  )
}
